.sidebar-box {
    background-color: var(--primaryColor);
    width: 18%;
    height: 100%;
    padding: 1rem;
    color: var(--secondaryTxtColor);
}

.sidebar-heading {
    color: var(--primaryTxtColor);
}

.sidebar-container {
    display: flex;
    flex-direction: column;
    padding: 0rem .3rem;
    gap: .6rem;
    margin-block: .8rem;
}

.sidebar-container>div {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 80%;
}

.hr {
    margin-block: .8rem;
}

/* -------------------------------RESPONSIVE--------------------------- */
@media all and (max-width: 760px) {
    .sidebar-box {
        width: 21%;
    }
}

@media all and (max-width: 650px) {
    .sidebar-box {
        display: var(--showSideBar);
        width: 24%;
    }
}

@media all and (max-width: 550px) {
    .sidebar-box {
        width: 100%;
    }
}