.color-container {
    display: flex;
    gap: 1rem;
    padding: 1rem;
}

.box {
    width: 10rem;
    height: 10rem;
    text-align: center;
    line-height: 10rem;
    color: whitesmoke;
    border: 1px solid black;
}

#box1 {
    background-color: var(--primaryColor);
}

#box2 {
    background-color: var(--secondaryColor);
}

#box3 {
    background-color: var(--primaryTxtColor);
}

#box4 {
    background-color: var(--secondaryTxtColor);
    color: black;
}

#box4 {
    background-color: var(--logoColor);
}