.nav {
    width: 100%;
    padding: 1rem;
    background-color: var(--primaryColor);
    cursor: pointer;

    display: flex;
    align-items: center;
    color: var(--secondaryTxtColor);

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.nav-menu {
    flex: 1;
    display: none;
    margin-right: 1rem;
}

#nav-menu {
    display: none;
}

.nav-logo {
    flex: 1;
    padding: 0rem 1rem;
    font-family: 'Rubik Iso', cursive;
    font-size: 30px;
    color: var(--logoColor);
}

.nav-search {
    flex: 1;
}

.nav-search>input {
    width: 100%;
    padding: .8rem 1rem;
    border-radius: 10px;
    border: none;
}

.nav-options {
    flex: 1;

    display: flex;
    justify-content: flex-end;
    gap: 1rem;
}

.nav-menu-search {
    background-color: var(--primaryColor);
    display: none;
    /* padding: 1rem 10%; */
}

.nav-menu-search>input {
    width: 80%;
    margin: 1rem 10%;
    padding: .6rem 1rem;
    border-radius: 10px;
    border: none;
}

/* -------------------------------RESPONSIVE--------------------------- */
@media all and (max-width: 650px) {
    .nav-menu {
        display: block;
    }

    .nav-logo {
        display: flex;
        /* justify-content: center; */
    }

    .nav-search {
        display: none;
    }

    .nav-menu-search {
        display: block;
        padding: 0% 10%;
    }
}

@media all and (max-width: 450px) {
    .nav-menu-search {
        padding: 0%;
    }
}